:root {
  /* fonts */
  --main-font: 'Roboto', sans-serif;

    /* text colors */
    --main-txt-cl: #2C2C2C;
    --green-cl: #BBF330;
    --green-hover-cl: #B5FF00;
    --dark-green-cl: #7EB101;
    --dark-green-hover-cl: #597D00;
    --white-cl: #FEFEFE;
    --black-cl: #000000;
    --default-yellow-cl: #FFE550;
    --bg-cl: #F5F5F5;
    --invalid-cl: #FF705D;
    --valid-cl: #7EB101;

  ---shadow-fruit: radial-gradient(
    50% 50% at 50% 50%,
    #2f583f 0%,
    rgba(105, 105, 105, 0) 100%
  );

  // /* other */
  --timing-function: 250ms cubic-bezier(0.4, 0, 0.2, 1);
}
